body {
  background-color: rgb(100, 99, 99);
  display: flex;
  align-items: center;
  justify-content: center; /* Center the container horizontally */
  padding: 15%;
  margin: 0;
  font-family: Arial, sans-serif; /* Adding a standard font */
}

.container {
  padding: 10px;
  border: 2px solid;
  width: 100%;
  max-width: 500px; /* Limit the container width */
  border-radius: 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: white;
  margin: 0 20px; /* Adjust the margin for responsiveness */
}

.head {
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
  text-align: center; /* Center the heading text */
}

input {
  padding: 10px;
  border: none; /* Remove the default border */
  border-radius: 10px;
  width: 100%; /* Make input take full width of container */
  max-width: 100%; /* Ensure input doesn't exceed container width */
  box-sizing: border-box; /* Include padding and border in element's width and height */
}

input:hover {
  background-color: rgb(173, 171, 171);
}

input:focus {
  border: 0px;
  background-color: beige;
}

/* Responsive design for smaller screens */
@media (max-width: 768px) {
  body {
    padding: 10%;
  }

  .container {
    width: 100%;
    max-width: 90%; /* Make the container occupy more width on smaller screens */
    margin-left: 0;
  }

  .head {
    font-size: 18px;
  }

  input {
    font-size: 16px;
    padding: 8px;
  }
}

@media (max-width: 480px) {
  body {
    padding: 5%;
  }

  .container {
    width: 100%;
    max-width: 100%;
  }

  .head {
    font-size: 16px;
  }

  input {
    font-size: 14px;
    padding: 6px;
  }
}
